import { Component, OnInit } from '@angular/core';
import { UserService } from 'src/app/auth/registro/user.service';
import { AuthenticationService } from 'src/app/services/authentication.service';
import { LoadingService } from 'src/app/services/loading/loading.service';

@Component({
  selector: 'app-register',
  templateUrl: './register.page.html',
  styleUrls: ['./register.page.scss'],
})
export class RegisterPage implements OnInit {
  newTeacher;
  constructor(private userService: UserService, private authService: AuthenticationService, private loading: LoadingService) { }

  ngOnInit() {
    this.newTeacher= {
      username: '',
      first_name: '',
      last_name: '',
      email: '',
      school_name: '',
      password1: '',
      password2: ''
    };  
  }
  registerNewTeacher(){
    this.loading.newTeacherLoading();
    this.userService.registerTeacher(this.newTeacher).subscribe(
      response => {
        //Finish user loading function
        this.loading.newTeachLoading.dismiss(); 
        console.log(response)
        alert('¡Buenas '+ this.newTeacher.username +', esperemos que disfrutes de esta nueva manera de enseñar!')},
      _error=> {setTimeout(()=>{
        //Finish map loading function
        this.loading.newTeachLoading.dismiss();
        //Give an alert message 
        alert('Vaya...Parece que no hemos podido añadirte como usuario.')
        //Set finish time to 1 sec after the error has happend.
      },1000)}
      );
  }
  logout(){
    this.authService.logout();
  }

}
